*
    font-family: sans-serif

body
    padding: 0px
    margin: 0px

main
    max-width: 800px
    margin: 40px auto
    padding: 25px
    background: #fff

    h1
        margin-top: 80px
        position: relative
        font-weight: 400

    p
        line-height: 1.6
        font-weight: 100
        text-align: justify

.topper
    position: relative
    width: 100%
    height: 260px
    overflow: hidden
    background: #0F006C
    z-index: -2

    img
        z-index: -1
        position: sticky
        top: 0px
        bottom: 0px
        left: 0px
        right: 0px
        object-fit: cover
        width: 100%
        height: calc(100vh - 100%)
        filter: brightness(50%) opacity(35%)
        // transform: perspective(5000px)

footer
    margin-top: 120px
    width: 100%
    height: 140px
    background: #f5f5f5
    color: #777
    display: flex
    align-items: center
    justify-content: center
    font-weight: 100
